@import '../variables/width-variables';

html {
	scroll-behavior: smooth;
}

* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
}

.visually-hidden {
	position: absolute;
	overflow: hidden;
	width: 1px;
	height: 1px;
	clip: rect(0 0 0 0);
}

.container-medium {
	margin: 0 auto;
	max-width: $desctope-width-medium;
}

.container-small {
	margin: 0 auto;
	max-width: $desctope-width-small;
}

.body {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-between;
	width: 100%;
	min-height: 100vh;

	&--scrolloff {
		overflow: hidden;
	}

	&--menu-open {
		overflow: hidden;

		&::before {
			content: '';
			position: fixed;
			z-index: 2;
			width: 100%;
			max-width: 100vw;
			background-color: rgb(255 255 255 / 60%);
			cursor: pointer;
			inset: 0;
		}
	}
}
