/* global styles */
html {
  scroll-behavior: smooth;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container-medium {
  margin: 0 auto;
  max-width: 1262px;
}

.container-small {
  margin: 0 auto;
  max-width: 1040px;
}

.body {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  min-height: 100vh;
}
.body--scrolloff {
  overflow: hidden;
}
.body--menu-open {
  overflow: hidden;
}
.body--menu-open::before {
  content: "";
  position: fixed;
  z-index: 2;
  width: 100%;
  max-width: 100vw;
  background-color: rgba(255, 255, 255, 0.6);
  cursor: pointer;
  inset: 0;
}

.heading {
  font-family: var(--heading-font-family);
  font-weight: var(--heading-basic-font-weight);
}
.heading--large {
  font-size: var(--heading-large-font-size);
  line-height: var(--heading-large-line-height);
}
.heading--medium {
  font-size: var(--heading-medium-font-size);
  line-height: var(--heading-medium-line-height);
}
.heading--small {
  font-size: var(--heading-small-font-size);
  line-height: var(--heading-small-line-height);
}

.paragraph {
  font-family: var(--paragraph-font-family);
  font-weight: var(--paragraph-basic-font-weight);
}
.paragraph--large {
  font-size: var(--paragraph-large-font-size);
  line-height: var(--paragraph-large-line-height);
}
.paragraph--medium {
  font-size: var(--paragraph-medium-font-size);
  line-height: var(--paragraph-medium-line-height);
}
.paragraph--small {
  font-size: var(--paragraph-small-font-size);
  line-height: var(--paragraph-small-line-height);
}

/* variables */
:root {
  --standart-side-padding: 20px;
}

:root {
  --white: #FFFFFF;
  --light: #F5F0EC;
  --tumbleweed: #DCC1AB;
  --green: #1B5B31;
  --dark: #111111;
}

:root {
  --heading-font-family: "Montserrat", "arial", sans-serif;
  --paragraph-font-family: "Inter", "arial", sans-serif;
  --heading-basic-font-weight: 500;
  --heading-large-font-size: 60px;
  --heading-large-line-height: 70px;
  --heading-medium-font-size: 48px;
  --heading-medium-line-height: 55px;
  --heading-small-font-size: 28px;
  --heading-small-line-height: 32px;
  --paragraph-basic-font-weight: 400;
  --paragraph-large-font-size: 16px;
  --paragraph-large-line-height: 24px;
  --paragraph-medium-font-size: 14px;
  --paragraph-medium-line-height: 21px;
  --paragraph-small-font-size: 12px;
  --paragraph-small-line-height: 18px;
}

/* components */
.header {
  position: relative;
  z-index: 2;
  padding: 0 var(--standart-side-padding);
  width: 100%;
  background-color: var(--white);
}

.header__wrap {
  display: grid;
  align-items: center;
  grid-template-columns: max-content 1fr;
  padding: 24px 0;
}

.header__title {
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 115px;
  height: 20px;
}

.header__logo-icon {
  width: 115px;
  height: 20px;
}

.main {
  width: 100%;
  height: 100%;
}

.nav-button {
  display: none;
  justify-self: flex-end;
  border: none;
  width: 40px;
  height: 40px;
  background-color: transparent;
  cursor: pointer;
}
@media (max-width: 900px) {
  .nav-button {
    z-index: 1;
    display: flex;
    align-items: center;
    justify-content: center;
  }
}

.nav-button__burger {
  transition: transform 400ms;
  cursor: pointer;
  user-select: none;
}
.nav-button__burger--active {
  transform: rotate(45deg);
}
.nav-button__burger--active > .nav-button__burger-line--top {
  stroke-dasharray: 17 82;
  stroke-dashoffset: -62px;
}
.nav-button__burger--active > .nav-button__burger-line--middle {
  stroke-dashoffset: 23px;
}
.nav-button__burger--active > .nav-button__burger-line--bottom {
  stroke-dashoffset: -83px;
}

.nav-button__burger-line {
  fill: none;
  stroke: #000000;
  stroke-linecap: round;
  stroke-width: 5.5;
  transition: stroke-dasharray 400ms, stroke-dashoffset 400ms;
}

.nav-button__burger-line--top {
  stroke-dasharray: 40 82;
}

.nav-button__burger-line--middle {
  stroke-dasharray: 40 111;
}

.nav-button__burger-line--bottom {
  stroke-dasharray: 40 161;
}

.nav {
  display: flex;
  align-items: center;
  justify-content: center;
  justify-self: flex-end;
  gap: 48px;
}
@media (max-width: 900px) {
  .nav {
    position: absolute;
    left: 0;
    right: 0;
    top: -350%;
    flex-direction: column;
    transition: all 0.5s ease-in;
  }
}

.nav--open {
  position: absolute;
  left: 0;
  right: 0;
  top: 0;
  justify-content: center;
  padding: 40px 20px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.6);
  background-color: var(--white);
  transition: all 0.5s ease;
}
.nav--open .nav__list {
  opacity: 1;
  transition: all 0.5s ease;
  pointer-events: all;
}
.nav--open .nav__search {
  opacity: 1;
  transition: all 0.5s ease;
  pointer-events: all;
}

.nav__list {
  display: flex;
  align-items: center;
  justify-content: space-between;
  list-style: none;
  gap: 48px;
}
@media (max-width: 900px) {
  .nav__list {
    flex-direction: column;
    align-items: center;
    justify-content: flex-start;
    width: 100%;
    opacity: 0;
    transition: all 0.5s ease;
    pointer-events: none;
  }
}

.nav__link {
  text-decoration: none;
  color: var(--dark);
}

@media (max-width: 900px) {
  .nav__search {
    opacity: 0;
    pointer-events: none;
  }
}

.search {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  transition: all ease 0.4s;
}
.search:hover > .search__input, .search:focus-within > .search__input {
  width: 160px;
  opacity: 1;
}

.search__input {
  border: 1px solid var(--dark);
  border-radius: 5px;
  padding: 5px 8px;
  width: 0;
  height: 100%;
  opacity: 0;
  transition: all ease 0.6s;
  pointer-events: 0;
}
@media (max-width: 900px) {
  .search__input {
    width: 160px;
    opacity: 1;
    pointer-events: all;
  }
}

.search__icon {
  width: 24px;
  height: 24px;
  fill: var(--dark);
}

.nav-with-submenu {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5px;
}
.nav-with-submenu:hover > .nav-with-submenu__list, .nav-with-submenu:focus-within > .nav-with-submenu__list {
  top: 100%;
  opacity: 1;
  pointer-events: all;
}

.nav-with-submenu__button {
  display: flex;
  align-items: center;
  align-self: stretch;
  justify-content: center;
  border: none;
  background-color: transparent;
}

.nav-with-submenu__icon {
  width: 12px;
  height: 12px;
  cursor: pointer;
}

.nav-with-submenu__list {
  position: absolute;
  left: 0;
  top: -100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  list-style: none;
  border-radius: 5px;
  padding: 10px 0;
  padding: 10px;
  background-color: var(--white);
  opacity: 0;
  transition: all ease 0.4s;
  pointer-events: none;
  gap: 5px;
}

.nav-with-submenu__item {
  display: flex;
}

.footer {
  padding: 80px var(--standart-side-padding);
  width: 100%;
  background-color: var(--dark);
}

.footer__wrap {
  display: grid;
  grid-row-gap: 120px;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(2, max-content);
}

.contacts {
  position: relative;
  display: grid;
  grid-row-gap: 62px;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(3, max-content);
}
.contacts::after {
  content: "";
  position: static;
  grid-row: 2/3;
  width: 100%;
  height: 1px;
  background-color: var(--light);
}

.contacts__header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
}
@media (max-width: 900px) {
  .contacts__header {
    flex-direction: column;
    justify-content: center;
  }
}

.contacts__header-logo {
  width: 115px;
  height: 20px;
}

.contacts__header-contact {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 24px;
}
@media (max-width: 900px) {
  .contacts__header-contact {
    flex-direction: column;
    gap: 10px;
  }
}

.contacts__header-description {
  color: var(--light);
}
@media (max-width: 900px) {
  .contacts__header-description {
    text-align: center;
  }
}

.contacts__header-button {
  border: none;
  border-radius: 200px;
  padding: 12px 24px 14px;
  color: var(--light);
  background-color: var(--green);
  cursor: pointer;
}

.contacts__info {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 48px;
}
@media (max-width: 900px) {
  .contacts__info {
    flex-direction: column;
    gap: 10px;
  }
}

.contacts__list {
  display: flex;
  flex-grow: 1;
  align-items: center;
  justify-content: flex-start;
  list-style: none;
  gap: 48px;
}
@media (max-width: 900px) {
  .contacts__list {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 15px;
  }
}

.contacts__item {
  display: flex;
}

.contacts__link {
  text-decoration: none;
  color: var(--light);
}

.contacts__tel {
  text-decoration: none;
  color: var(--light);
}

.contacts__email {
  text-decoration: none;
  color: var(--light);
}

.copyright {
  display: flex;
  align-items: center;
  justify-content: space-between;
  gap: 20px;
}
@media (max-width: 900px) {
  .copyright {
    flex-direction: column;
  }
}

.copyright__info {
  color: var(--light);
}

.copyright__author {
  display: flex;
  align-items: center;
  justify-content: space-between;
  text-decoration: none;
  color: var(--light);
  gap: 16px;
}

.copyright__icon {
  width: 113px;
  height: 23px;
}

.contact-banner {
  padding: 60px 0;
  width: 100%;
}
@media (max-width: 900px) {
  .contact-banner {
    padding: 0;
  }
}

.contact-banner__wrap {
  display: grid;
  align-items: flex-end;
  grid-column-gap: 64px;
  grid-template-columns: 1fr max-content;
  grid-template-rows: 1fr;
  padding: 120px 110px;
  background-color: var(--green);
}
@media (max-width: 900px) {
  .contact-banner__wrap {
    align-items: center;
    grid-row-gap: 30px;
    grid-template-columns: 1fr;
    padding: 100px 50px;
  }
}
@media (max-width: 600px) {
  .contact-banner__wrap {
    padding: 100px 20px;
  }
}

.contact-banner__info {
  display: flex;
  flex-direction: column-reverse;
  align-items: flex-start;
  justify-content: center;
}
@media (max-width: 900px) {
  .contact-banner__info {
    align-items: center;
    text-align: center;
  }
}

.contact-banner__title {
  font-weight: 400;
  font-style: normal;
  font-size: 40px;
  letter-spacing: -2px;
  color: var(--light);
}
.contact-banner__title > span {
  font-weight: 500;
  font-style: italic;
  letter-spacing: 1px;
}

.contact-banner__subtitle {
  font-weight: 400;
  font-style: normal;
  font-size: 40px;
  letter-spacing: -2px;
  color: var(--light);
}

.contact-banner__action {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  gap: 24px;
}
@media (max-width: 900px) {
  .contact-banner__action {
    flex-direction: row;
    align-items: center;
    justify-content: center;
  }
}
@media (max-width: 600px) {
  .contact-banner__action {
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
}

.contact-banner__action-name {
  color: var(--light);
}

.contact-banner__link {
  border-radius: 200px;
  padding: 10px 24px 12px;
  text-decoration: none;
  color: var(--green);
  background-color: var(--light);
}

.about {
  padding-right: 20px;
  width: 100%;
  background-color: var(--green);
}
@media (max-width: 900px) {
  .about {
    padding-right: 0;
  }
}

.about__wrap {
  display: grid;
  grid-template-columns: 1fr 1fr;
  width: 100%;
  background-color: var(--green);
}
@media (min-width: 1262px) {
  .about__wrap {
    grid-template-columns: 1fr 631px 651px 1fr;
    grid-template-rows: 740px;
  }
}
@media (max-width: 900px) {
  .about__wrap {
    display: flex;
    flex-direction: column;
  }
}

.about__info {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  grid-column: 2/3;
  grid-row: 1/-1;
  padding: 135px 0 135px 160px;
}
@media (min-width: 1262px) {
  .about__info {
    grid-column: 3/4;
  }
}
@media (max-width: 900px) {
  .about__info {
    padding: 40px var(--standart-side-padding) 40px var(--standart-side-padding);
  }
}

.about__title {
  padding-bottom: 16px;
  color: var(--light);
}

.about__subtitle {
  padding-bottom: 40px;
  color: var(--light);
}

.about__text {
  padding-bottom: 72px;
  color: var(--light);
}

.about__link {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid var(--light);
  border-radius: 200px;
  padding: 12px 22px 14px;
  width: max-content;
  text-decoration: none;
  color: var(--light);
  gap: 8px;
}

.about__gallery {
  display: flex;
  align-items: center;
  justify-content: center;
  grid-column: 1/2;
  grid-row: 1/-1;
}
@media (min-width: 1262px) {
  .about__gallery {
    grid-column: 1/3;
  }
}
@media (max-width: 900px) {
  .about__gallery {
    height: 400px;
  }
}

.about__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.offer {
  padding: 120px var(--standart-side-padding) 160px var(--standart-side-padding);
  width: 100%;
  background-color: var(--light);
}

.offer__wrap {
  display: grid;
  grid-row-gap: 96px;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(2, max-content);
}

.offer__header {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  padding-left: 80px;
}
@media (max-width: 900px) {
  .offer__header {
    padding-left: 0;
  }
}

.offer__title {
  padding-bottom: 16px;
  color: var(--green);
}

.offer__subtitle {
  padding-bottom: 32px;
  color: var(--dark);
}

.offer__additional-info {
  color: var(--dark);
}

.offers-list {
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  list-style: none;
  width: 100%;
  gap: 64px;
}
@media (max-width: 900px) {
  .offers-list {
    grid-template-columns: 1fr;
  }
}

.offers-list__item {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: space-between;
  border-radius: 28px;
  padding: 48px 40px;
  background-color: var(--white);
  gap: 63px;
}
.offers-list__item:hover {
  box-shadow: 10px 10px 20px rgba(163, 177, 198, 0.5), -10px -10px 20px rgba(255, 255, 255, 0.5);
}

.offers-list__header {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 34px;
}

.offers-list__icon-wrap {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 44px;
}

.offers-list__offer-icon {
  object-fit: contain;
}

.offers-list__header-titles {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-end;
  gap: 12px;
}

.offers-list__offer-title {
  color: var(--dark);
}

.offers-list__offer-description {
  color: var(--dark);
}

.offers-list__offer-link {
  display: flex;
  align-items: center;
  justify-content: center;
  border-bottom: 1px solid var(--green, #1B5B31);
  width: max-content;
  text-decoration: none;
  color: var(--green);
  gap: 10px;
}

.offers-list__block-link {
  position: absolute;
  z-index: 1;
  width: 100%;
  height: 100%;
  background-color: transparent;
  inset: 0;
}

.main-banner {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 737px;
  background-color: var(--tumbleweed);
}
@media (max-width: 900px) {
  .main-banner {
    height: 100%;
  }
}

.swiper {
  position: relative;
  width: 100%;
  height: 100%;
}

.swiper-wrapper {
  width: 100%;
  min-width: 100vw;
}

.swiper-controls {
  position: absolute;
  right: 0;
  bottom: 0;
  z-index: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 24px 32px;
  background-color: var(--white);
  gap: 32px;
}

.swiper-controls__btn {
  position: static;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: 0;
  width: 48px;
  height: 48px;
}
.swiper-controls__btn::after {
  display: none;
}
.swiper-controls__btn > svg {
  width: 24px;
}

.swiper-slide {
  background-color: var(--tumbleweed);
}

.swiper-slide__item {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding-left: 20px;
  width: 100%;
  height: 100%;
}
@media (min-width: 1262px) {
  .swiper-slide__item {
    grid-template-columns: 1fr 651px 631px 1fr;
    grid-template-rows: 740px;
  }
}
@media (max-width: 900px) {
  .swiper-slide__item {
    display: flex;
    flex-direction: column;
    padding-left: 0;
  }
}

.swiper-slide__info {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
  grid-column: 1/2;
  grid-row: 1/-1;
  padding-right: 64px;
}
@media (min-width: 1262px) {
  .swiper-slide__info {
    grid-column: 2/3;
  }
}
@media (max-width: 900px) {
  .swiper-slide__info {
    padding: 50px var(--standart-side-padding) 50px var(--standart-side-padding);
  }
}

.swiper-slide__title {
  padding-bottom: 44px;
  color: var(--dark);
}

.swiper-slide__text {
  padding-bottom: 72px;
  color: var(--dark);
}

.swiper-slide__item-controls {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  gap: 36px;
}
@media (max-width: 900px) {
  .swiper-slide__item-controls {
    gap: 20px;
  }
}

.swiper-slide__link {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 200px;
  padding: 12px 24px 14px;
  width: max-content;
  text-decoration: none;
  color: var(--light);
  background: var(--green);
  gap: 10px;
}

.swiper-slide__button {
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid var(--green);
  border-radius: 200px;
  padding: 12px 22px 14px;
  width: max-content;
  text-decoration: none;
  color: var(--green);
  background-color: transparent;
  cursor: pointer;
  gap: 8px;
}

.swiper-slide__gallery {
  display: flex;
  align-items: center;
  justify-content: center;
  grid-column: 2/-1;
  grid-row: 1/-1;
}
@media (min-width: 1262px) {
  .swiper-slide__gallery {
    grid-column: 3/-1;
  }
}
@media (max-width: 900px) {
  .swiper-slide__gallery {
    height: 300px;
  }
}

.swiper-slide__image {
  width: 100%;
  height: 100%;
  object-fit: cover;
}